import React, { useEffect, useRef } from 'react'
import Module from './learningSubCompenents/Module'
import './styles/Courses/coursesstyle.css'
import { useState } from 'react'
import axios from 'axios'
const Courses = () => {
    axios.defaults.withCredentials = true
    const [onAdd, setOnAdd] = useState(false)
    const [classes, setClasses] = useState([])
    const classeName = useRef("")
    const message = useRef("")
    const createClass = ()=>{
        axios.post("/class/createclass",{
            className:classeName.current.value
        }).then((res)=>{
            classeName.current.value=""
        }).catch((err)=>{
            message.current.innerText=err.response.data.message
        })
    }
    const getClasses = ()=>{
        axios.get("/class/allclasses").then((res)=>{
            setClasses(res.data.result)
        })
    }
    useEffect(()=>{
        getClasses()
    },[])
  return (
    <div className='b-courses'>
        <div className='top'>
            <h5 className='heading' >Courses</h5>
            <p className='add-class' onClick={()=>{setOnAdd(!onAdd)}}>{onAdd?'Hide' : 'Add a class +'}</p>
        </div>
        {onAdd && <div  >
            <div className='add-class-box'>
                <input type='search' placeholder='Enter class name' ref={classeName}/>
                <h1 className='plus' onClick={createClass} >+</h1>
            </div>
            <p ref={message}></p>
        </div>

        }
        <div className='modules'>
            {
                classes.map((Class)=>{
                    return <Module module={Class.className} code={Class.classCode} />
                })
            }
        </div>
    </div>
  )
}

export default Courses